import PropTypes from 'prop-types';
import { useRef, useState } from 'react';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Grid, Stack, Card, Popover, Button, Typography, CardHeader, CardContent } from '@mui/material';
// _mock
import _mock from '../../_mock';
// components
import Image from '../../components/Image';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';
import { CarouselArrowIndex } from '../../components/carousel';
import Scrollbar from '../../components/Scrollbar';

// ----------------------------------------------------------------------

ProductDescrip.propType = {
  productId: PropTypes.number.isRequired,
}

export default function ProductDescrip({ productId }) {

  const carouselRef = useRef(null);
  const theme = useTheme();

  const [popover, setPopover] = useState(null);


  // ---------------------FAKE---------
  const productData = {
    title: _mock.text.title(7),
    price: _mock.number.price(5),
    description: _mock.text.description(9),
    id: _mock.id(5),
  }

  const videoData = [...Array(4)].map((_, index) => ({
    light: _mock.image.product(index),
    video: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4",
    description: _mock.text.description(index),
    id: _mock.id(index),
  }));

  // ----------------------------------------

  const settings = {
    dots: false,
    arrows: true,
    autoplay: false,
    slidesToShow: 3,
    rtl: Boolean(theme.direction === 'rtl'),
    beforeChange: (current, next) => setCurrentIndex(next),

  };

  const [currentIndex, setCurrentIndex] = useState(0);

  const handleHoverOpen = (event) => {
    setPopover(event.currentTarget);
  }
  const handleHoverClose = () => {
    setPopover(null);
  }
  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };


  return (
    // <Stack>
      <Box sx={{ position: 'relative', p: 3 }}>

        <Typography variant="h5" sx={{}}>
          {productData.title}
        </Typography>
        <Typography variant="h6" sx={{color:"error.main"}}>
          $ {productData.price}
        </Typography>
        <Typography variant="h6" sx={{}}>
          {"Description"}
        </Typography>
        <CardContent  sx={{p:2, width:"100%", height:"30vh"}}>
          <Scrollbar>
            {productData.description}
            {productData.description}
            {productData.description}
          </Scrollbar>
        </CardContent>

        <Slider ref={carouselRef} {...settings}>
          {videoData.map((item, index) => (
            <div key={index}
              aria-owns={popover ? item.id : undefined}
              aria-haspopup="true"
              onMouseEnter={handleHoverOpen}
              onMouseLeave={handleHoverClose}
            >
              <MotionInView key={index} variants={varFade().in} sx={{ px: 0.5, py: 1 }}>
                <Image ratio="1/1" src={item.light} onClick={() => { console.log('channel') }} sx={{ borderRadius: 1, cursor: "point" }} />
              </MotionInView>
              <Popover id={item.id}
                open={Boolean(popover)}
                anchorEl={popover}
                anchorOrigin={{ vertical: 'top', horizontal: 'left', }}
                transformOrigin={{ vertical: 'top', horizontal: 'right', }}
                onClose={handleHoverClose}
                disableRestoreFocus
                sx={{
                  pointerEvents: 'none',
                }}
              >
                <Box sx={{ maxWidth: 280 }}>
                  <video muted width="100%" height="auto" autoPlay>
                    <source src={item.video} type="video/mp4" />

                  </video>
                  {/* <ReactPlayer width={280} heigt={160} muted playing loop url={item.video} sx={{ p: 0, m: 0 }} /> */}
                  <TextMaxLine line={5} sx={{ m: 2 }}>
                    {item.description}
                  </TextMaxLine>
                </Box>
              </Popover >
            </div>
          ))}
        </Slider>
        <CarouselArrowIndex
          index={currentIndex}
          total={videoData.length}
          onNext={handleNext}
          onPrevious={handlePrevious}
        />
      </Box>

    // </Stack>
  );
}




