import { sentenceCase } from 'change-case';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
// @mui
import { Box, Tab, Card, Grid, Divider, Container, Typography } from '@mui/material';
import { TabContext, TabList, TabPanel } from '@mui/lab';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getProduct, addCart, onGotoStep } from '../../redux/slices/product';
// routes
// hooks
import useSettings from '../../hooks/useSettings';
import useOffSetTop from '../../hooks/useOffSetTop';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import { SkeletonProduct } from '../../components/skeleton';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import {
  ProductDetailsSummary,
  ProductDetailsReview,
  ProductDetailsCarousel,
  ProductDetailsVideo,
  ProductByCustomer,
  ProductAddToCart,
} from '../../sections/product-custom';
import { LanPopularChannels, LanRecommendProducts } from '../../sections/customer';


// ----------------------------------------------------------------------
import _mock from '../../_mock';


// ----------------------------------------------------------------------

export default function ProductDetail() {
  const { themeStretch } = useSettings();
  const showAddToCart = useOffSetTop(500);
  const dispatch = useDispatch();
  const [value, setValue] = useState('1');
  const { name = '' } = useParams();
  // const { product, error, checkout } = useSelector((state) => state.product);
  // fake product data
  const index = 4;
  const product = {
    images: [...Array(5)].map((_, index) => (_mock.image.product(index))),
    id: _mock.id(index),
    name: _mock.text.title(index),
    // sizes,
    price: _mock.number.price(index),
    cover: _mock.image.cover(index),
    status: 'sale',
    // color,
    available: 7,
    priceSale: _mock.number.price(index),
    totalRating: _mock.number.rating(index),
    totalReview: _mock.number.price(index + 4),
    inventoryType: 'in_stock',

    //
    reviews: [...Array(21)].map((_, index) => ({
      id: _mock.id(index),
      name: _mock.name.fullName(),
      rating: 4,
      comment: _mock.text.sentence(index),
      helpful: 9,
      postedAt: _mock.time(index),
      avatarUrl: _mock.image.avatar(index),
      isPurchaseda: true,
    })),
    //
    description: _mock.text.description(index),
    //
    ratings: [
      { name: 1, starCount: 445, reviewCount: 344 },
      { name: 2, starCount: 23, reviewCount: 432 },
      { name: 3, starCount: 435, reviewCount: 45 },
      { name: 4, starCount: 45, reviewCount: 353 },
      { name: 5, starCount: 234, reviewCount: 2342 },
    ]
  }
  const error = false;
  const checkout = {
    activeStep: 0,
    cart: [],
    subtotal: 0,
    total: 0,
    discount: 0,
    shipping: 0,
    billing: null,
  };
  useEffect(() => {
    dispatch(getProduct(name));
  }, [dispatch, name]);

  const handleAddCart = (product) => {
    dispatch(addCart(product));
  };

  const handleGotoStep = (step) => {
    dispatch(onGotoStep(step));
  };

  return (
    <Page title="Product Details">
      <Container maxWidth={themeStretch ? false : 'lg'} sx={{ position: 'relative' }}>


        {product && (
          <Card>
            <Grid container>
              <Grid item xs={12} md={4} >
                <ProductDetailsCarousel product={product} />
              </Grid>
              <Grid item xs={12} md={4}>
                <ProductDetailsSummary
                  product={product}
                  cart={checkout.cart}
                  onAddCart={handleAddCart}
                  onGotoStep={handleGotoStep}
                />
              </Grid>
              <Grid item xs={12} md={4}>
                <ProductDetailsVideo productId={product.id} />
              </Grid>
            </Grid>
          </Card>
        )}
        {!product && <SkeletonProduct />}
        {error && <Typography variant="h6">404 Product not found</Typography>}

        <ProductByCustomer />
        <LanRecommendProducts />
        <LanPopularChannels />
        <ProductDetailsReview product={product} />
        {showAddToCart &&
          <ProductAddToCart product={product} cart={checkout.cart} />}

      </Container>
    </Page>
  );
}
