import PropTypes from 'prop-types';
import { useRef } from 'react';
import Slider from 'react-slick';
// @mui
import { useTheme } from '@mui/material/styles';
import { Box, Stack, Paper, Button, Avatar, Container, Typography } from '@mui/material';

// components
import Image from '../../components/Image';
import Label from '../../components/Label';
import { CarouselArrows } from '../../components/carousel';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';
// ----------------------------------------------------------------------
import _mock from '../../_mock';

ProductByCustomer.propTypes = {
  customerId: PropTypes.string,
}


export default function ProductByCustomer({ customerId }) {
  const carouselRef = useRef(null);
  const theme = useTheme();

  // fakedata //
  const customerProducts = [...Array(10)].map((_, index) => ({
    cover: _mock.image.cover(index),
    productName: _mock.text.title(index),
    price: _mock.number.price(index),
    id: _mock.id(index),
  }));

  const settings = {
    arrows: false,
    slidesToShow: 4,
    centerMode: true,
    centerPadding: '0px',
    rtl: Boolean(theme.direction === 'rtl'),
    responsive: [
      {
        breakpoint: 1279,
        settings: { slidesToShow: 3 },
      },
      {
        breakpoint: 959,
        settings: { slidesToShow: 2 },
      },
      {
        breakpoint: 600,
        settings: { slidesToShow: 1 },
      },
    ],
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };

  return (
    <Container sx={{ pb: 2 }}>
      <Typography variant="h4" sx={{ mb: 1 }}>
        {'More Produty by MERN STLK'}
      </Typography>
      <Box sx={{ position: 'relative' }}>
        <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
          <Slider ref={carouselRef} {...settings}>
            {customerProducts.map((item, index) => (
              <ShowCard key={index} item={item} />
            ))}
          </Slider>
        </CarouselArrows>
      </Box>
    </Container>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  item: PropTypes.shape({
    price: PropTypes.number,
    cover: PropTypes.string,
    productName: PropTypes.string,
    id: PropTypes.string,
  }),
};


function ShowCard({ item }) {
  const theme = useTheme();
  const { productName, cover, price, id } = item;
  // const router =useRouter();
  return (
    <Paper sx={{
      my: 2,mx:2 , borderRadius: 1, bgcolor: 'background.neutral',
      '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}` }
    }}>
      <Box sx={{ position: 'relative' }}>
        <Image src={cover} ratio="1/1" sx={{ borderRadius: 1 }} />
        <Stack spacing={0} sx={{ left: 16, zIndex: 9, bottom: 10, position: 'absolute', }}>
          <TextMaxLine sx={{ color: '#fff' }} line={1}>{productName}</TextMaxLine>
          <Typography sx={{ color: '#fff', display: 'block' }}>{`price: $${price}`}</Typography>
        </Stack>
      </Box>
    </Paper>
  );
}
